@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.delivery {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  flex-grow: 1;
  @include media(tablet){
    flex-grow: 0;
    flex-basis: 60%;
  }
  &__data {
    display: flex;
    flex-direction: column;
    flex-basis: 40%;
    padding: 2rem;
    height: min-content;
    @include media(tablet){
      flex-basis: 60%;
    }
  }
  &__label {
    font-size: 2rem;
    font-weight: $font-weight;
    color: $light-gray;
    margin-top: 1rem;
    @include media(tablet){
      font-size: 2.5rem;
    }
    &--small{
      font-size: 1.4rem;
      color: $light-gray;
      margin-left: 2rem;
      @include media(tablet){
        font-size: 2rem;
      }
      }
    }
    &__input {
      width: 90%;
      font-weight: 300;
      height: 2.2rem;
      padding: .8rem;
      padding-left: 1.5rem;
      font-size: 1.7rem;
      margin: 0.8rem;
      border-radius: 30px;
      border: 1px solid $light-gray;
      @include media(tablet){
        font-size: 2rem;
        // width: 70%;
      }
      &:focus{
        outline: 2px solid $main-color;
        border: none;
      }
  }
  &__method{
    display: flex;
    flex-direction: column;
  }
}
